  min-width: 768px;
  overflow: visible;
  height: 100%;
}

.header {
  overflow: hidden;
  background: rgba(103, 58, 183, 0.3);
  box-shadow: 1px 1px 10px #111;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 900;
  width: 100%;
  font-variant: small-caps;
  height: 75px;
  padding: 0 1em 0;
  font-size: 2em;
}

