  color: #fff;
  display: inline-block;
  cursor: pointer;
  height: 70px;
  height: 65px;
  font-weight: bold;
  padding: 20px 0px 10px 0;
  margin-left: 30px;
}

.header label:hover {
  border-bottom: solid 5px gainsboro;
  box-sizing: border-box;
  height: 65px;
  color: gainsboro;
  text-shadow: 1px 1px 1px #111;
  transition: all 0.3s;
  height: 60px;
  color: gainsboro;
  text-shadow: 1px 1px 1px #111;
  transition: all 0.3s;
}

.logo {
  float: left;
  line-height: 60px;
}

.slide {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 100%;
  z-index: 10;
  padding: 80px 1em 0;
  transition: left 0s 0.75s;
  background-size: cover;
  background-position: 50% 50%;
  overflow: hidden;
}

